:root {
  /* Primary: Deep Navy (professional, trustworthy) */
  --navy-400: #627d98;
  --navy-500: #486581;
  --navy-600: #334e68;
  --navy-700: #243b53;
  --navy-800: #1a2f44;
  --navy-900: #102a43;
  --navy-950: #0a1929;

  /* Primary: Mint Green (fresh, modern) */
  --mint-300: #6EE7B7;
  --mint-400: #4ECDC4;
  --mint-500: #3EB489;
  --mint-600: #059669;

  /* Accent: Coral (complementary, warm) */
  --coral-400: #FF8A80;
  --coral-500: #FF6B6B;
  --coral-600: #F85149;

  /* Secondary: Teal (growth, positive) */
  --teal-400: #2dd4bf;
  --teal-500: #14b8a6;
  --teal-600: #0d9488;

  /* Financial Semantics */
  --profit: #059669;
  --profit-light: #10b981;
  --loss: #dc2626;
  --loss-light: #ef4444;
  --neutral: #6b7280;

  /* Legacy teal mapping for compatibility */
  --primary: #14b8a6;
  --primary-light: #2dd4bf;
  --primary-dark: #0d9488;
}

/* Light Theme (Default) */
html.light, :root {
  --background: #f8fafc;
  --foreground: #0f172a;
  --card: #ffffff;
  --card-foreground: #0f172a;
  --popover: #ffffff;
  --popover-foreground: #0f172a;
  --primary-bg: #0f766e;
  --primary-fg: #ffffff;
  --secondary: #f1f5f9;
  --secondary-fg: #0f172a;
  --muted: #f1f5f9;
  --muted-fg: #64748b;
  --accent: #f1f5f9;
  --accent-fg: #0f172a;
  --border-color: #e2e8f0;
  --input-bg: #ffffff;
  --ring: #4ECDC4;

  /* Surfaces */
  --bg-dark: #f8fafc;
  --bg-card: #ffffff;
  --bg-elevated: #f1f5f9;
  --bg-surface: #e2e8f0;
  --border: #e2e8f0;
  --border-light: #cbd5e1;

  /* Text */
  --text-primary: #0f172a;
  --text-secondary: #475569;
  --text-muted: #64748b;
}

/* Dark Theme */
html.dark {
  --background: #0a1929;
  --foreground: #f8fafc;
  --card: #0f2137;
  --card-foreground: #f8fafc;
  --popover: #0f2137;
  --popover-foreground: #f8fafc;
  --primary-bg: #4ECDC4;
  --primary-fg: #0a1929;
  --secondary: #1e3a5f;
  --secondary-fg: #f8fafc;
  --muted: #1e3a5f;
  --muted-fg: #94a3b8;
  --accent: #162a45;
  --accent-fg: #f8fafc;
  --border-color: #1e3a5f;
  --input-bg: #0f2137;
  --ring: #4ECDC4;

  /* Surfaces */
  --bg-dark: #0a1929;
  --bg-card: #0f2137;
  --bg-elevated: #162a45;
  --bg-surface: #1a3352;
  --border: #1e3a5f;
  --border-light: #2d4a6f;

  /* Text */
  --text-primary: #f8fafc;
  --text-secondary: #94a3b8;
  --text-muted: #64748b;
}

/* Utility classes for theme */
.bg-background { background-color: var(--background); }
.bg-card { background-color: var(--card); }
.bg-muted { background-color: var(--muted); }
.text-foreground { color: var(--foreground); }
.text-muted-foreground { color: var(--muted-fg); }
.border-border { border-color: var(--border-color); }

* {
  box-sizing: border-box;
}

body {
  font-family: 'Inter', system-ui, -apple-system, sans-serif;
  background: var(--background);
  color: var(--foreground);
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Tabular numbers for financial data */
.tabular-nums {
  font-variant-numeric: tabular-nums;
  font-feature-settings: "tnum";
}

.font-mono-finance {
  font-family: 'JetBrains Mono', 'SF Mono', Consolas, monospace;
  font-variant-numeric: tabular-nums;
}

/* Professional Neumorphic shadows */
.neu-shadow {
  box-shadow:
    8px 8px 20px rgba(0, 0, 0, 0.5),
    -4px -4px 16px rgba(255, 255, 255, 0.02);
}

.neu-shadow-sm {
  box-shadow:
    4px 4px 10px rgba(0, 0, 0, 0.4),
    -2px -2px 8px rgba(255, 255, 255, 0.02);
}

.neu-inset {
  box-shadow:
    inset 4px 4px 10px rgba(0, 0, 0, 0.5),
    inset -2px -2px 8px rgba(255, 255, 255, 0.01);
}

/* Professional glow effects */
.glow-navy {
  box-shadow: 0 0 20px rgba(72, 101, 129, 0.3);
}

.glow-mint {
  box-shadow: 0 0 20px rgba(78, 205, 196, 0.3);
}

.glow-mint-lg {
  box-shadow: 0 0 40px rgba(78, 205, 196, 0.4);
}

.glow-coral {
  box-shadow: 0 0 20px rgba(255, 107, 107, 0.3);
}

.glow-teal {
  box-shadow: 0 0 20px rgba(20, 184, 166, 0.3);
}

.glow-profit {
  box-shadow: 0 0 15px rgba(5, 150, 105, 0.3);
}

.glow-loss {
  box-shadow: 0 0 15px rgba(220, 38, 38, 0.3);
}

/* Animations */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; }
}

@keyframes slideUp {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes slideIn {
  from { opacity: 0; transform: translateX(-20px); }
  to { opacity: 1; transform: translateX(0); }
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes scaleIn {
  from { opacity: 0; transform: scale(0.9); }
  to { opacity: 1; transform: scale(1); }
}

@keyframes chartDraw {
  from { stroke-dashoffset: 100; }
  to { stroke-dashoffset: 0; }
}

@keyframes countUp {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.animate-fade-in { animation: fadeIn 0.5s ease-out; }
.animate-fade-out { animation: fadeOut 0.5s ease-out forwards; }
.animate-slide-up { animation: slideUp 0.5s ease-out; }
.animate-slide-in { animation: slideIn 0.3s ease-out; }
.animate-pulse { animation: pulse 2s ease-in-out infinite; }
.animate-float { animation: float 3s ease-in-out infinite; }
.animate-spin-slow { animation: spin 3s linear infinite; }
.animate-scale-in { animation: scaleIn 0.3s ease-out; }
.animate-chart-draw { animation: chartDraw 1s ease-out forwards; }
.animate-count-up { animation: countUp 0.4s ease-out; }

/* Professional gradient backgrounds */
.bg-gradient-radial {
  background: radial-gradient(ellipse at center, rgba(72, 101, 129, 0.1) 0%, transparent 70%);
}

.bg-gradient-mesh {
  background:
    radial-gradient(at 40% 20%, rgba(72, 101, 129, 0.15) 0px, transparent 50%),
    radial-gradient(at 80% 0%, rgba(78, 205, 196, 0.08) 0px, transparent 50%),
    radial-gradient(at 0% 50%, rgba(72, 101, 129, 0.1) 0px, transparent 50%),
    radial-gradient(at 80% 50%, rgba(20, 184, 166, 0.08) 0px, transparent 50%),
    radial-gradient(at 0% 100%, rgba(72, 101, 129, 0.1) 0px, transparent 50%);
}

.bg-gradient-finance {
  background: linear-gradient(135deg, var(--navy-950) 0%, var(--navy-900) 50%, var(--navy-800) 100%);
}

.bg-gradient-gold {
  background: linear-gradient(135deg, var(--mint-500) 0%, var(--mint-400) 50%, var(--mint-300) 100%);
}

.bg-gradient-navy-gold {
  background: linear-gradient(135deg, var(--navy-900) 0%, var(--navy-800) 60%, rgba(78, 205, 196, 0.2) 100%);
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-dark);
}

::-webkit-scrollbar-thumb {
  background: var(--border);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--navy-600);
}

/* Button styles - Primary Gold */
.btn-primary {
  border-radius: 0.75rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-weight: 600;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 200ms;
  background: linear-gradient(135deg, var(--mint-400) 0%, var(--mint-500) 100%);
  color: var(--navy-950);
  box-shadow: 0 4px 14px rgba(78, 205, 196, 0.3);
}

.btn-primary:hover {
  box-shadow: 0 6px 20px rgba(78, 205, 196, 0.5);
  transform: translateY(-1px);
}

.btn-primary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* Button styles - Secondary */
.btn-secondary {
  border-radius: 0.75rem;
  background-color: transparent;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-weight: 500;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 200ms;
  border: 1px solid var(--border-light);
  color: var(--text-secondary);
}

.btn-secondary:hover {
  border-color: var(--mint-400);
  color: var(--mint-300);
}

/* Button styles - Teal (for positive actions) */
.btn-teal {
  border-radius: 0.75rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-weight: 600;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 200ms;
  background: linear-gradient(135deg, var(--teal-500) 0%, var(--teal-600) 100%);
  color: var(--navy-950);
  box-shadow: 0 4px 14px rgba(20, 184, 166, 0.3);
}

.btn-teal:hover {
  box-shadow: 0 6px 20px rgba(20, 184, 166, 0.5);
  transform: translateY(-1px);
}

/* Card styles */
.card {
  background: var(--card);
  border: 1px solid var(--border-color);
  border-radius: 1rem;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

.card-elevated {
  background: var(--bg-elevated);
  border: 1px solid var(--border-color);
  border-radius: 1rem;
}

.card-gold {
  background: var(--card);
  border: 1px solid rgba(78, 205, 196, 0.3);
  border-radius: 1rem;
}

.card:hover {
  border-color: var(--border-light);
}

/* Input styles */
.input {
  width: 100%;
  border-radius: 0.75rem;
  padding: 1rem;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 200ms;
  background: var(--input-bg);
  border: 1px solid var(--border-color);
  color: var(--foreground);
}

.input::-moz-placeholder {
  color: var(--text-muted);
}

.input::placeholder {
  color: var(--text-muted);
}

.input:focus {
  border-color: var(--mint-400);
  outline: none;
  box-shadow: 0 0 0 3px rgba(78, 205, 196, 0.1);
}

/* Financial data display */
.metric-positive {
  color: var(--profit-light);
}

.metric-negative {
  color: var(--loss-light);
}

.metric-neutral {
  color: var(--text-secondary);
}

.metric-highlight {
  color: var(--mint-300);
}

/* Badge styles */
.badge {
  border-radius: 9999px;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  font-size: 0.75rem;
  line-height: 1rem;
  font-weight: 500;
}

.badge-gold {
  background: rgba(78, 205, 196, 0.15);
  color: var(--mint-300);
  border: 1px solid rgba(78, 205, 196, 0.3);
}

.badge-navy {
  background: rgba(72, 101, 129, 0.15);
  color: var(--navy-400);
  border: 1px solid rgba(72, 101, 129, 0.3);
}

.badge-teal {
  background: rgba(20, 184, 166, 0.15);
  color: var(--teal-400);
  border: 1px solid rgba(20, 184, 166, 0.3);
}

.badge-profit {
  background: rgba(5, 150, 105, 0.15);
  color: var(--profit-light);
  border: 1px solid rgba(5, 150, 105, 0.3);
}

.badge-loss {
  background: rgba(220, 38, 38, 0.15);
  color: var(--loss-light);
  border: 1px solid rgba(220, 38, 38, 0.3);
}

/* Difficulty badges */
.badge-beginner {
  background: rgba(5, 150, 105, 0.15);
  color: #10b981;
}

.badge-intermediate {
  background: rgba(78, 205, 196, 0.15);
  color: var(--mint-300);
}

.badge-advanced {
  background: rgba(220, 38, 38, 0.15);
  color: #f87171;
}

/* Output format badges */
.badge-excel {
  background: rgba(34, 139, 34, 0.15);
  color: #22c55e;
  border: 1px solid rgba(34, 139, 34, 0.3);
}

.badge-powerpoint {
  background: rgba(234, 88, 12, 0.15);
  color: #fb923c;
  border: 1px solid rgba(234, 88, 12, 0.3);
}

.badge-pdf {
  background: rgba(220, 38, 38, 0.15);
  color: #f87171;
  border: 1px solid rgba(220, 38, 38, 0.3);
}

.badge-memo {
  background: rgba(99, 102, 241, 0.15);
  color: #a5b4fc;
  border: 1px solid rgba(99, 102, 241, 0.3);
}

/* Tooltip arrow */
.tooltip-arrow {
  position: absolute;
  width: 12px;
  height: 12px;
  background: inherit;
  border: inherit;
  border-right: none;
  border-bottom: none;
  transform: rotate(45deg);
}

/* Finance-specific table styles */
.finance-table {
  width: 100%;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-variant-numeric: tabular-nums;
}

.finance-table th {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  padding-left: 1rem;
  padding-right: 1rem;
  text-align: left;
  font-weight: 500;
  color: var(--text-secondary);
  border-bottom: 1px solid var(--border);
}

.finance-table td {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  padding-left: 1rem;
  padding-right: 1rem;
  border-bottom: 1px solid var(--border);
}

.finance-table tr:hover td {
  background: rgba(72, 101, 129, 0.1);
}

/* KPI Card */
.kpi-card {
  border-radius: 0.75rem;
  padding: 1rem;
  background: var(--bg-card);
  border: 1px solid var(--border);
}

.kpi-card-highlight {
  border-color: rgba(78, 205, 196, 0.3);
  background: linear-gradient(135deg, var(--bg-card) 0%, rgba(78, 205, 196, 0.05) 100%);
}

/* Progress indicator */
.progress-bar {
  height: 0.5rem;
  overflow: hidden;
  border-radius: 9999px;
  background: var(--bg-dark);
}

.progress-bar-fill {
  height: 100%;
  border-radius: 9999px;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 500ms;
  background: linear-gradient(90deg, var(--mint-500) 0%, var(--mint-300) 100%);
}

.progress-bar-teal .progress-bar-fill {
  background: linear-gradient(90deg, var(--teal-600) 0%, var(--teal-400) 100%);
}

/* Sparkline styles */
.sparkline {
  stroke: var(--mint-400);
  stroke-width: 2;
  fill: none;
}

.sparkline-area {
  fill: rgba(78, 205, 196, 0.1);
}

